<template>
  <div id="app">
    <div :class="{fix:isFix,head:ishead}">
      <div>
        <img src="./img/ball.png" alt>
      </div>
      <ul class="fl">
        <li v-for="(item,index) in json" @click="fenlei(index)" :key="index">{{index}}</li>
      </ul>
    </div>
    <div class="body">
      <router-view/>
    </div>
    <div title="返回顶部" @click="toTop" class="toTop">
      <img src="./img/top.png" alt>
    </div>
  </div>
</template>

<script>
// import { mapActions } from "vuex";
// import { mapGetters } from "vuex";
export default {
  name: "App",
  data() {
    return {
      isFix: false,
      ishead: true
    };
  },
  computed: {
    // ...mapGetters(["getOne", "getTwo"])
  },
  watch: {
    
  },
  methods: {
    // ...mapActions(["setOne", "setTwo"]),
    fenlei(a) {
      sessionStorage.getOne=a
      // this.right = this.json[a];
      // this.setOne(a);
      this.$router.push({
        path: "/"
      });
    },
    toTop() {
      var id = setInterval(() => {
        var Y = window.scrollY - 80;
        if (Y <= 0) {
          Y = 0;
          clearInterval(id);
          window.scrollTo({ top: Y });
        } else {
          window.scrollTo({ top: Y });
        }
      }, 3);
    },
    scroll() {
      if (window.scrollY >= 100) {
        this.isFix = true;
      } else {
        this.isFix = false;
      }
    }
  },
  created() {
    // this.setOne("关于前端");
    sessionStorage.getOne='关于前端'
    window.addEventListener("scroll", this.scroll);
  }
};
</script>

<style lang='less' scoped>
#app {
  .fix {
    position: fixed;
    top: 0;
    width: 100%;
    background: rgba(255, 255, 255, 0.94);
    margin: 0;
    left: 0;
    height: 60px !important;
  }
  background: white;
  // position: relative;
  border-radius: 5px;
  max-width: 1000px;
  // width: 1000px;
  margin: 20px auto;
  padding-bottom: 20px;
  box-shadow: 0 0 12px #9e9e9e;
  .head {
    transition: all 0.5s;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #dadee1;
    height: 80px;
    margin: 0 0 30px 0;
    box-sizing: border-box;
    padding: 0 30px;

    img {
      height: 60px;
      // margin-left: 20px;
      // margin-top: 20px;
    }
    .fl {
      display: flex;
      align-items: flex-start;
      li {
        margin: 0 30px;
        cursor: pointer;
        transition: all 0.5s;
      }
      li:hover {
        border-bottom: 1px solid red;
        color: red;
      }
    }
  }
  .body {
    // width: 100%;
    margin: 0 40px;
  }
  .toTop {
    position: fixed;
    right: 50px;
    bottom: 50px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 3px;
    padding: 3px;
    img {
      width: 30px;
      // border: 1px solid ;
      cursor: pointer;
      // height: 20px;
    }
  }
}
</style>



