import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
  //状态集合
  state: {
    num: 0,
    one: '',
    two: ''
  },
  // 突变集合
  mutations: {
    SET_ONE(state, n) {
      state.one = n;
    },
    SET_TWO(state, n) {
      // setTimeout(() => {

      state.two = n;
      // }, 1000);
    },
  },
  //   指令集合
  actions: {
    setOne({commit}, n) {
      commit('SET_ONE', n)
    },
    setTwo(context, n) {
      context.commit('SET_TWO', n)
    },
  },
  //   显示集合
  getters: {
    getOne: (state) => state.one,
    getTwo: (state) => state.two
  },
})



