import Vue from 'vue'
import Router from 'vue-router'
import Article from '@/components/article'
import Index from '@/components/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/article',
      name: 'article',
      component: Article
    },

  ]
})



