<template>
  <div class="index">
    <div v-for="(item,index) in indexData" :key="index">
      <ul @click="toArticle(index)" class="title">{{item['title']}}</ul>
      <ul class="date">发布日期：{{item['time']}}</ul>
      <ul class="introduce">{{item['introduce']}}</ul>
    </div>
  </div>
</template>

<script>
// import { mapGetters, mapActions } from "vuex";
import { Default } from "../json/json";
export default {
  data() {
    return {
      indexData: ""
    };
  },
  methods: {
    // ...mapActions(["setOne", "setTwo"]),
    toArticle(a) {
      // this.setTwo(a);
      sessionStorage.getTwo=a
      this.$router.push({
        path: "/article",
        query:{
          getOne:sessionStorage.getOne,
          getTwo:sessionStorage.getTwo
        }
      });
    }
  },
  computed: {
    // ...mapGetters(["getOne", "getTwo"])
  },
  created() {
    this.indexData = Default;
  },
  watch: {
    getOne(a){
      this.indexData = this.json[a];
    }
  }
};
</script>

<style scoped lang='less'>
.index {
  div {
    border: 1px solid #a3a3a3;
    border-radius: 5px;
    width: 50%;
    padding: 20px;
    box-sizing: border-box;
    margin: 20px;
    .title {
      font-size: 20px;
      cursor: pointer;
      color: #9ec09f;
    }
    .date {
      color: #686868;
      font-size: 12px;
      line-height: 2;
    }
    .introduce {
      font-size: 14px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  div:hover {
    .title {
      color: red;
    }
  }
}
</style>



