<template>
  <div class="wc">
    <div class="right">
      <ul class="flwz">目录</ul>
      <ul class="lb">
        <li v-for="(item,index) in right" :key="index">
          <p @click="ml(index)">{{item['title']}}</p>
          <span>发布日期:</span>
          <span>{{item['time']}}</span>
        </li>
      </ul>
    </div>
    <div class="left">
      <div class="article">
        <ul class="dh">{{getOne}} / {{getTwo}}</ul>
        <div class="head">
          <h1>{{title}}</h1>
          <span>发布日期:</span>
          <span class="rq">{{time}}</span>
          <span>&nbsp;&nbsp;&nbsp;&nbsp;分类：{{getOne}}</span>
        </div>
        <div class="body" v-html="html"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { mapActions } from "vuex";
export default {
  name: "HelloWorld",
  data() {
    return {
      html: "",
      title: "",
      time: "",
      right: {}, //右侧导航
      getOne: "",
      getTwo: ""
    };
  },
  methods: {},
  computed: {},
  created() {
    this.getOne = this.$route.query.getOne;
    this.getTwo = this.$route.query.getTwo;
    this.right = this.json["关于前端"];
    var data = () =>
      import(`../data/${this.json[this.getOne][this.getTwo]["content"]}`);
    data().then(a => {
      this.title = a.data.title;
      this.html = a.data.comtent;
      this.time = a.data.time;
    });
  },
  watch: {
    getTwo() {
      var data = () =>
        import(`../data/${this.json[this.getOne][this.getTwo]["content"]}`);
      data().then(a => {
        this.title = a.data.title;
        this.html = a.data.comtent;
        this.time = a.data.time;
      });
    }
  }
};
</script>

<style scoped lang='less'>
@media screen and (max-width: 720px) {
  .wc {
    display: block;
  }
  .left,
  .right {
    width: 100%;
  }
  .right{
    height: auto;
    padding: 10px 0;
  }
}
@media screen and (min-width: 720px) {
  .wc {
    display: flex;
  }
  .left {
    width: 70%;
  }
  .right {
    width: 27%;
    height: 450px;
  }
}
.wc {
  // display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;

  .left {
    // width: 70%;
    padding-bottom: 20px;
    box-sizing: border-box;
    .article {
      .head {
        margin: 0 0 20px 0;
        span {
          color: #686868;
          line-height: 2;
        }
        h1 {
          line-height: 2;
        }
      }
      .dh {
        color: #8d9095;
        margin-bottom: 30px;
      }
    }
  }
  .right {
    // width: 27%;
    
    box-sizing: border-box;
    // border: 1px solid red;
    background: #f5f5f5;
    border-radius: 10px;
    .flwz {
      font-size: 20px;
      font-weight: bolder;
      margin: 10px 0 0 10px;
    }
    .lb {
      li {
        border-bottom: 1px solid #e5e5e5;
        margin: 7px 10px;
        span {
          color: #686868;
          font-size: 12px;
        }
        p {
          font-weight: bold;
          font-size: 14px;
          cursor: pointer;
          line-height: 30px;
        }
      }
    }
  }
}
</style>



